<template>
  <v-container
    fluid
    class="address"
    v-resize="resize"
  >
    <v-row no-gutters>
      <v-col
        cols="12"
        class="text-center pa-4 title"
      >
        Información de la dirección
      </v-col>
      <v-col
        cols="12"
        sm="6"
        class="px-4 py-0"
      >
        <v-autocomplete
          ref="input_address"
          v-bind="form.address.props"
          v-model="form.address.value"
          :items="locationFoundItems"
          :search-input.sync="locationSearchText"
          item-text="value"
          item-value="id"
          hide-no-data
          return-object
        >
        </v-autocomplete>
      </v-col>
      <v-col
        cols="12"
        class="px-4 py-0"
        sm="6"
      >
        <v-text-field
          v-bind="form.references.props"
          v-model="form.references.value"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        class="pa-4 py-0"
      >
        <GmapMap
          class="pa-3"
          ref="mapRef"
          :center="center"
          :zoom="10"
          map-type-id="terrain"
          style="width: 100%; height: 500px"
        >
          <GmapMarker
            :position="{
              lat: markerPosition.lat,
              lng: markerPosition.lng,
            }"
            :clickable="true"
            :draggable="true"
            @dragend="updateLocation"
            :paths="coveragePolygon"
            :path="coveragePolygon"
          />
        </GmapMap>
      </v-col>
      <v-col
        cols="12"
        class="pa-4 pt-0 subtitle-2"
      >
        ¿Está bien el mapa? Puedes cambiar la ubicación.
      </v-col>
      <v-row
        no-gutters
        class="pa-4"
        justify="space-around"
      >
        <v-col
          cols="12"
          class="text-center subtitle-1"
        >
          Esta es la dirección de mi:
        </v-col>
        <v-col
          :cols="'6'"
          sm="3"
          md="3"
          v-for="(alias, index) in aliasArray"
          :key="index"
          align="center"
          class="pa-4"
        >
          <v-card
            max-width="150"
            max-height="90"
            class="px-1 py-4 transition-delay"
            @click="selectAlias(alias.title)"
            :color="aliasSelected == alias.title ? 'primary' : 'white'"
          >
            <v-container
              fill-height
              class="pa-0"
            >
              <v-row no-gutters>
                <v-col
                  cols="12"
                  alig="center"
                >
                  <v-icon
                    x-large
                    :color="aliasSelected == alias.title ? 'white' : 'primary'"
                  >{{ alias.icon }}</v-icon>
                </v-col>
              </v-row>
              <v-row no-gutters>
                <v-col
                  cols="12"
                  class="text-center text-uppercase font-weight-bold "
                  :class="[
                    aliasSelected == alias.title
                      ? 'white--text'
                      : 'primary--text',
                    smallScreen ? 'caption' : ' ',
                  ]"
                >
                  {{ alias.title }}
                </v-col>
              </v-row>
            </v-container>
          </v-card>
        </v-col>
      </v-row>
      <v-col
        v-if="aliasSelected == 'Otro'"
        cols="12"
        class="px-4 py-0"
      >
        <v-text-field
          v-bind="form.other.props"
          v-model="form.other.value"
        ></v-text-field>
      </v-col>
      <v-col
        cols="12"
        class="text-center pa-4"
      >
        <v-btn
          :disabled="!validForm"
          color="primary"
          width="200"
          @click="saveAddressInfo"
        >Guardar</v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import {
  GetSuggestions,
  GetLocation,
  GetAddressByLatLng,
  DrawPolygon,
} from "@/services/MapsPlacesAutocomplete.js";
import { errorMessage } from "@/services/MessageService";
import Address from "@/models/address";
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      smallScreen: false,
      locationSearchText: null,
      locationEntries: [],
      center: {
        lat: 17.98398998452883,
        lng: -92.93958413281251,
      },
      markerPosition: {
        lat: 17.98398998452883,
        lng: -92.93958413281251,
      },
      aliasSelected: "",
      form: Address.getAddressForm(),
      map: null,
    };
  },
  async mounted() {
    await this.getCoverages();
    const map = await this.$refs.mapRef.$mapPromise;
    DrawPolygon(this.coveragePolygon, map);
  },
  computed: {
    ...mapState("address", ["aliasArray", "addressData", "coveragePolygon"]),
    locationFoundItems() {
      return this.locationEntries;
    },
    validForm() {
      if (
        this.aliasSelected.length > 0 &&
        this.form.address.value.value &&
        this.form.references.value.length > 0 &&
        this.markerPosition.lat &&
        this.markerPosition.lng
      ) {
        if (this.aliasSelected == "Otro" && this.form.other.value.length <= 0) {
          return false;
        } else {
          return true;
        }
      } else {
        return false;
      }
    },
  },
  watch: {
    async locationSearchText(newVal) {
      try {
        // Call the method from the previous section here
        const result = await GetSuggestions(newVal);
        this.locationEntries = result;
      } catch (error) {
        errorMessage(error.code);
      }
    },
    async "form.address.value"(newVal) {

      const location = await GetLocation(newVal.value);
      this.form.address.value.location = {
        latitude: location.lat(),
        longitude: location.lng(),
      };
      this.center = location;
      this.markerPosition = { lat: location.lat(), lng: location.lng() };
    },
  },
  methods: {
    ...mapActions("address", ["saveUserAddress", "getCoverages"]),
    resize() {
      if (window.innerWidth < 375) {
        this.smallScreen = true;
      } else {
        this.smallScreen = false;
      }
    },
    selectAlias(alias) {
      this.aliasSelected = alias;
    },
    async updateLocation(location) {
      this.markerPosition = {
        lat: location.latLng.lat(),
        lng: location.latLng.lng(),
      };
      this.center = this.markerPosition;
      const exactAddress = await GetAddressByLatLng(this.markerPosition);
      if(this.form.address.value.value){
        this.form.address.value.value = exactAddress.formatted_address;
        this.form.address.value.id = exactAddress.place_id;
        this.form.address.location = {
          latitude: exactAddress.geometry.location.lat(),
          longitude: exactAddress.geometry.location.lng(),
        }
      }else{
        this.form.address.value = {}
        this.form.address.value.value = exactAddress.formatted_address;
        this.form.address.value.id = exactAddress.place_id;
        this.form.address.location = {
          latitude: exactAddress.geometry.location.lat(),
          longitude: exactAddress.geometry.location.lng(),
        }
      }
      const result = await GetSuggestions(exactAddress.formatted_address);
      this.locationEntries = result
    },
    saveAddressInfo() {
      const addressData = {
        alias:
          this.aliasSelected == "Otro"
            ? this.form.other.value
            : this.aliasSelected,
        address: this.form.address.value.value,
        references: this.form.references.value,
        latitude: this.markerPosition.lat.toString(),
        longitude: this.markerPosition.lng.toString(),
      };
      this.saveUserAddress(addressData);
      this.form.references.value = "";
      this.form.other.value = "";
      this.$router.push({ name: "home" });
    },
  },
};
</script>
<style lang="css" scoped>
.address {
  max-width: 1080px;
}
.transition-delay {
  transition: 500ms;
}
</style>
